.dialogs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 100%;
}

.dialogsItems {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: min-content;
    border: 1px solid var(--theme-default-text-color-header);
    border-radius: var(--border-radius);
    margin-right: 5px;
    font-size: var(--font-size-small);
}

.chatContainer {
    flex: 9;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 15px 15px;
    background-color: var(--theme-default-background-color-header);
    width: 100%;
    min-height: 412px;
    border-radius: var(--border-radius);
}

.dialog {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 3px;
    margin: 7px 5px;
    color: var(--theme-default-text-color-header);
    text-decoration: none;
}

.dialog img {
    width: 50px;
    height: 50px;
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
}

.messages {
    padding: 10px;
}

.messageContainer {
    display: flex;
    flex-direction: column;
    row-gap: 2px;
}

.message {
    line-height: 0.5;
}

.chatMessageTimestamp {
    font-size: var(--font-size-small);
}

.chat {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.me {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.friend {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.addNewMessageContainer {
    display: flex;
    flex-direction: column;

}

.selectUserLink {
    color: darkred;
}